@mixin mobileMedium() {
  @media (min-width: 380px) {
    @content;
  }
}

@mixin tablet() {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin desktop() {
  @media (min-width: 1200px) {
    @content;
  }
}

@mixin titles() {
  font-size: 2rem;
  padding: 30px;
  text-shadow: 2px 2px 4px $colorBlue;
}
